export class hexToHSL {
  constructor(hex) {
    this.hex = hex;
    this.H_Value = "";
    this.S_Value = "";
    this.L_Value = "";
    this.init();
  }

  init() {
    let result = /^#?([a-f\d]{2})([a-f\d]{2})([a-f\d]{2})$/i.exec(this.hex);

    let r = parseInt(result[1], 16);
    let g = parseInt(result[2], 16);
    let b = parseInt(result[3], 16);

    r = r / 255;
    g = g / 255;
    b = b / 255;

    let max = Math.max(r, g, b),
      min = Math.min(r, g, b);
    let h,
      s,
      l = (max + min) / 2;

    if (max == min) {
      h = s = 0; // achromatic
    } else {
      let d = max - min;
      s = l > 0.5 ? d / (2 - max - min) : d / (max + min);
      switch (max) {
        case r:
          h = (g - b) / d + (g < b ? 6 : 0);
          break;
        case g:
          h = (b - r) / d + 2;
          break;
        case b:
          h = (r - g) / d + 4;
          break;
      }
      h /= 6;
    }

    s = s * 100;
    s = Math.round(s);
    l = l * 100;
    l = Math.round(l);
    h = Math.round(360 * h);

    this.H_Value = h;
    this.S_Value = s;
    this.L_Value = l;
  }

  getColor(s = this.S_Value, l = this.L_Value, h = this.H_Value) {
    let colorInHSL = "hsl(" + h + ", " + s + "%, " + l + "%)";
    return colorInHSL;
  }

  getHValue() {
    return this.H_Value;
  }
  getSValue() {
    return this.S_Value;
  }
  getLValue() {
    return this.L_Value;
  }
}
